import React from 'react';
import Quote from './Quotes.js';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-grid.css';
import 'bootstrap/dist/css/bootstrap-reboot.css';
import Sortbar from './Sortbar.js';
import ProfileSidebar from './ProfileSidebar.js';
import {  Row, Navbar,  Container,  Col, Button, InputGroup, FormControl } from 'react-bootstrap';


export default class Quotesman extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			sortByMostLiked:false,
			 keyID : localStorage.getItem('CurrentQuotesmanAccountKeyID'),
			 posts:[]
		}
		clearIfAnyAccountDeleted();
		this.handleLatest = this.handleLatest.bind(this);
		this.handleTrending = this.handleTrending.bind(this);
		this.handleMostLiked  = this.handleMostLiked.bind(this);
	
	}
	componentDidMount(){
		if(this.state.keyID===null){
			this.setState({posts:[]});
		}
		else{
		let myAccount = JSON.parse(localStorage.getItem(this.state.keyID));
		let myPosts = myAccount.myPosts;

		for(let f of myAccount.friends){
			let friend = JSON.parse(localStorage.getItem(f));
			let posts = friend.myPosts;
			myPosts.concat(posts);
		}
		this.setState({posts:myPosts});
	}
	}

	handleMostLiked(e){
		e.preventDefault();
		console.log(e);
	}
	handleLatest(e){
		e.preventDefault();
		console.log(e);
	}
	handleTrending(e){
		e.preventDefault();
		console.log(e);
	}
	render(){
		return( 
				<Row>
					<Col xs = {3}>
						<ProfileSidebar/>						
					</Col>
				
					<Col>
						<Navbar bg = 'light' variant = 'outline-light' className = 'm-0'>
							<Navbar.Brand href = '#home' as='h1'>Quotesman</Navbar.Brand>
								<InputGroup>

								<FormControl type = 'text'
								 placeholder = 'search for person '
								 />
								 <InputGroup.Append>
								 	<Button variant = 'primary'>Search</Button>
								 </InputGroup.Append>
								</InputGroup>
						</Navbar>
						<Container >
						{
							this.state.posts.map((item)=>(
								<Quote key = {item.postID} quote = {item}/>
								))
						}
						</Container>
					</Col>
                    <Col xs = {3}>
                    	<Sortbar handleMostLiked = {this.handleMostLiked} handleLatest = {this.handleLatest} handleTrending = {this.handleTrending} />
                    </Col>					
				</Row>	
			);
	}
}

function clearIfAnyAccountDeleted(){
	let qAccountsKeyIDs = JSON.parse(localStorage.getItem('QAccountsKeyIDs')) || [];

	for(let i = qAccountsKeyIDs.length-1; i>=0; i--){
		if(localStorage.getItem(qAccountsKeyIDs[i])===null){
			qAccountsKeyIDs.splice(i, 1);
		}

	}
	localStorage.setItem('QAccountsKeyIDs', JSON.stringify(qAccountsKeyIDs));
}