import React, { useState } from 'react';
import { Button, Modal } from 'react-bootstrap';
import MessageList from '../Messages/MessageList.js';
export default function MessageListButton(props){
	const [ showMessageList, setShowMessageList ] = useState(false);
	return(
		<>
			<Button block variant = 'outline-success' onClick = {()=>{setShowMessageList(true)}}> Messages </Button>

			<Modal
				show = {showMessageList}
				onHide = {()=>{ setShowMessageList(false) }}
				size = 'lg'
				centered
				backdrop  = 'static'
				>
					<Modal.Header closeButton>
					</Modal.Header>
						<MessageList keyID = {props.keyID}/>
			</Modal>		
		</>
		);
}