import React from 'react';
import { ListGroup } from 'react-bootstrap';
import ProfileBar from './ProfileBar.js';
import AcceptRequestButton from '../QButtons/AcceptRequestButton.js';
export default class RequestReceived extends React.Component{
	constructor(props){
		super(props);
		this.state  = {
			requestReceivedList:[]
		}
		this.removeThisFromList = this.removeThisFromList.bind(this);
	}
	componentDidMount(){
		let myAccount = JSON.parse(localStorage.getItem(this.props.keyID));
		let requests = myAccount.requests;
		let requestReceivedList = [];
		for(let keyID of requests){
			requestReceivedList.push(JSON.parse(localStorage.getItem(keyID)));
		}
		this.props.sendOutSide(requestReceivedList);
		this.setState({requestReceivedList});
	}
	removeThisFromList(keyID){
		let requestReceivedList = this.state.requestReceivedList;
		for(let ac of requestReceivedList){
			if(ac.keyID===keyID){
				requestReceivedList.splice(requestReceivedList.indexOf(ac), 1);
			}
		}
		this.props.updateFriendListBy(keyID);
		this.setState({requestReceivedList});
	}
	render(){
		return(
			<>
				{
					this.props.requestReceivedAccounts.map((account)=>(
						<ListGroup.Item key = {account.keyID}>
							<ProfileBar dpwidth = {'50'} keyID = {account.keyID} />
							<AcceptRequestButton removeThisFromList = {()=>{this.removeThisFromList(account.keyID)}} me = {this.props.keyID} friend = {account.keyID}/>
						</ListGroup.Item>
					))
				}
			</>
			);
	}
}