import React from 'react';
import { Card } from 'react-bootstrap';
import MyFriends from './MyFriends.js';  
import GlobalAccounts from './GlobalAccounts.js';
import RequestSent from './RequestSent.js';
import RequestReceived from './RequestReceived.js';
export default class Friends2 extends React.Component {
	constructor(props){
		super(props);
		console.log(this.props.keyID);
		this.state = {
			parentProfile:false,
			keyID:this.props.keyID,
			friendList:[],
			suggestedAccounts:[],
			requestSentAccounts:[],
			requestReceivedAccounts:[],
			aisehi:'aisehi'
		}
	}
	componentDidMount(){
		let parentKeyID = localStorage.getItem('CurrentQuotesmanAccountKeyID');
		if(parentKeyID===this.state.keyID){
			this.setState({parentProfile:true});
		}
	}

	render(){
		return(
			<Card>
				<Card.Header>
					<h1> this is header  {this.state.aisehi} </h1>
				</Card.Header>
				<Card.Body>
					<h3> Friends </h3>
					<MyFriends 
					keyID = {this.props.keyID}
					sendOutSide = {(friendList)=>{this.setState({friendList:friendList})}}
					friendList = {this.state.friendList}

					/>
					
					<h3> Requests </h3>
					<RequestReceived 
					keyID = {this.props.keyID}
					sendOutSide = {(requests)=>{this.setState({requestReceivedAccounts:requests})}}
					requestReceivedAccounts = {this.state.requestReceivedAccounts}
						
					updateFriendListBy = {(keyID)=>{
						let friendList = this.state.friendList;
						friendList.push(JSON.parse(localStorage.getItem(keyID)));
						this.setState({friendList});
					}}

					/>

					<h3>RequestSent </h3>
					<RequestSent
					 keyID = {this.props.keyID}
					 updateGlobalListBy = {(keyID)=>{
					 	let suggestedAccounts = this.state.suggestedAccounts;
					 	suggestedAccounts.push(JSON.parse(localStorage.getItem(keyID)));
					 	this.setState({suggestedAccounts});
					 }}

					 sendOutSide = {(rsa)=>{this.setState({requestSentAccounts:rsa})}}
					 requestSentAccounts = {this.state.requestSentAccounts}
					  />
					<h3> Suggested Friends </h3>
					<GlobalAccounts 
					keyID = {this.props.keyID} 
					sendOutSide = {(sal)=>{this.setState({suggestedAccounts:sal})}} 
					suggestedAccounts = { this.state.suggestedAccounts }
					
					updateRequestSentListBy = {(keyID)=>{
						let requestSentAccounts = this.state.requestSentAccounts;
						requestSentAccounts.push(JSON.parse(localStorage.getItem(keyID)));
						this.setState({requestSentAccounts});
					}}
					 />
				</Card.Body>
			</Card>
			);
	}
}

function getAccountByKeyID(keyID){
	return JSON.parse(localStorage.getItem(keyID));
}