import React from 'react';
import { ListGroup } from 'react-bootstrap';
import ProfileBar from './ProfileBar.js';
import DenyRequestButton from '../QButtons/DenyRequestButton';

function clearIfAccountDeleted(keyID){
	let myProfile = JSON.parse(localStorage.getItem(keyID));
	let requestSentAccounts = myProfile.requestSentAccounts;
}

export default class RequestSent extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			requestSentAccounts:[]
		}
		this.removeThisFromRequestSent = this.removeThisFromRequestSent.bind(this);
	}
	componentDidMount(){
		let myProfile = JSON.parse(localStorage.getItem(this.props.keyID));
		let requestSentAccountKeys = myProfile.requestSentTo;
		let requestSentAccounts = [];
		
		for(let i = requestSentAccountKeys.length-1; i>=0; i--){
			if(localStorage.getItem(requestSentAccountKeys[i])===null){
				requestSentAccountKeys.splice(i, 1);
			}
			else{
			requestSentAccounts.push(JSON.parse(localStorage.getItem(requestSentAccountKeys[i])));				
			}
		}
		myProfile.requestSentTo = requestSentAccountKeys;
		localStorage.setItem(this.props.keyID, JSON.stringify(myProfile));
		this.props.sendOutSide(requestSentAccounts);
		this.setState({requestSentAccounts});
	}
	removeThisFromRequestSent(friendKey){
		let requestSentAccounts = this.state.requestSentAccounts;
		for(let ac of requestSentAccounts){
			if(friendKey===ac.keyID){
				requestSentAccounts.splice(requestSentAccounts.indexOf(ac),1);
			}
		}
		this.props.updateGlobalListBy(friendKey);
		this.setState({requestSentAccounts});
	}
	render(){
		return(
			<div>
				{
					this.state.requestSentAccounts.map((account)=>(
						<ListGroup.Item key = {account.keyID} >
							<ProfileBar dpwidth = {'50'} keyID = {account.keyID}/>
							<DenyRequestButton me = {this.props.keyID} friend = {account.keyID} removeThisFromRequestSent = {()=>{this.removeThisFromRequestSent(account.keyID)}}/>
						</ListGroup.Item>
						))
				}				
			</div>
			);
	}
}