import React from 'react';
import { Button } from 'react-bootstrap';

export default class AcceptRequestButton extends React.Component{
	constructor(props){
		super(props);
		this.acceptFriendRequest = this.acceptFriendRequest.bind(this);
	}
	acceptFriendRequest(e){
		e.preventDefault();
		let myAccount = JSON.parse(localStorage.getItem(this.props.me));
		let friends = myAccount.friends;
		if(!friends.includes(this.props.friend)){
			friends.push(this.props.friend);
		}
		myAccount.friends = friends;
		
		let requests = myAccount.requests;
		if(requests.includes(this.props.friend)){
			requests.splice(requests.indexOf(this.props.friend), 1);
		}
		myAccount.requests = requests;

		localStorage.setItem(this.props.me, JSON.stringify(myAccount));
	
		let friendAccount = JSON.parse(localStorage.getItem(this.props.friend));
		 friends = friendAccount.friends;
		if(!friends.includes(this.props.me)){
			friends.push(this.props.me);
		}
		friendAccount.friends = friends;

		let requestSentTo = friendAccount.requestSentTo;
		if(requestSentTo.includes(this.props.me)){
			requestSentTo.splice(requestSentTo.indexOf(this.props.me), 1);
		}
		friendAccount.requestSentTo = requestSentTo;
		localStorage.setItem(this.props.friend, JSON.stringify(friendAccount));
		this.props.removeThisFromList();
	}
	render(){
		return(
			<Button variant = 'dark' onClick = {this.acceptFriendRequest}> Accept Request </Button>
			);
	}
}