import React from 'react';
import { Card,ListGroup, Alert, InputGroup, FormControl, Badge,   Image, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-grid.css';
import 'bootstrap/dist/css/bootstrap-reboot.css';
import ProfileBar from './Friends/ProfileBar.js';
import LikeButton from './LikeButton.js';
import CommentButton from './CommentButton.js';
import AddToLibrary from './AddToLibrary.js';
export default class Quote extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			quoteID:this.props.quote,
			stInd:0,
			showCommentBox:false,
			canChangeLang:false,
			canShowExplain:false,
			likes:9,
			likedBy:[],
			quote:JSON.parse(localStorage.getItem(this.props.quote))
		}
		this.handleComment = this.handleComment.bind(this);
		this.handleLang = this.handleLang.bind(this);
		this.handleExplain = this.handleExplain.bind(this);
	}
	componentDidMount(){
		let quote = JSON.parse(localStorage.getItem(this.props.quote));
		this.setState({quote});
	}
	handleComment(e){
		e.preventDefault();
		this.setState(state=>({
			showCommentBox:!state.showCommentBox
		}));
	}
	handleLang(e){
		e.preventDefault();
		this.setState(state=> ({ canChangeLang:!state.canChangeLang}));
	}
	handleExplain(e){
		e.preventDefault();
		this.setState(state=>({ canShowExplain:!state.canShowExplain}));
	}
	render(){
		return(
			<Card className = 'mt-4 mb-3'>

				<Card.Header className = "d-flex flex-row justify-content-between">
				<ProfileBar dpwidth = {'50'} keyID = {this.state.quote.uploaderID}/>
					<Button variant = 'danger' size= 'sm ' className = 'btn-circle' >{'Follow'}</Button> 

			 </Card.Header>
				<Card.Body>
					<blockquote className = 'blockquote mb-0'>
						<p> {  this.state.quote.statement[this.state.stInd] }
						</p>
						<footer className = 'blockquote-footer text-right'>
							{this.state.quote.quoter}
						</footer>
					</blockquote>
				</Card.Body>
				<Card.Footer className = " d-flex flex-row justify-content-between">
					<LikeButton quote = {this.props.quote} />

					<Button variant = 'success' onClick = {this.handleComment} className = 'btn-circle'>{' Comment '}</Button>
					<Button variant = 'warning' onClick = {this.handleLang} className = 'btn-circle'>{'Lang '}</Button>

					<Button variant = 'dark' onClick = {this.handleExplain } className = 'btn-circle'>{'Explain '}</Button>
					<AddToLibrary keyID  = {this.state.quote.uploaderID} quoteID = {this.state.quote.quoteID}/>
				</Card.Footer>
				<Alert show = {this.state.showCommentBox} >
						<CommentButton postID = {this.props.quote} commentor = {this.state.quote.uploadedBy} commentorID = {this.state.quote.uploaderID} />
				</Alert>
				<Alert show = {this.state.canChangeLang} className = 'mb-0'>
					<div className = 'd-flex flex-row justify-content-between'>
						<Button variant = 'outline-secondary' size = 'sm'> English </Button>
						<Button variant = 'outline-danger'    size = 'sm'> Hindi </Button>
						<Button variant = 'outline-success'   size = 'sm'> Urdu </Button>
					</div>
				</Alert>
				<Alert show = {this.state.canShowExplain} >
					<Card.Body>
						<Card.Text>
							{ this.state.quote.Explain.length===0?"No Explaination": this.state.quote.Explain.map((item)=>(
									<span key = {Date.now()}> {item }</span>
								)) }
						</Card.Text>
					</Card.Body>
				</Alert>				
			</Card>
			);
	}
}


	let quote = {
				uploadedBy:'ahnd234521',
				likedBy:['slahd34242'],
				date:'23rd August 2020',
				taggedFriend:['dkdei34567'],
				likes:1,
				category:"following passion",
				comments:[
					{
						commentor:'dhnd320596',
						comment:'nice',
						date:'22nd July 2020'
					},
					{
						commentor:'Aishan Raza',
						comment:'awesome',
						date:'21st March 2020'
					}
					],
				statement:['Dreams are not those which you see while sleep	Dreams are that which do not let you sleep', 'Sapne wo nhi jo aap sote waqt dekhte ho sapne wo hain jo aapko sone nhi dete',
				'Sapne wo nhi jo aap sote waqt dekhte ho sapne wo hain jo aapko sone nhi dete' ],
				quoter:'APJ Abdul Kalam',
				Explain:['dream are that ' , 'iska matlab yeh hai ki ' , 'iska matlab yeh hai ki ' ]						
	};

