import React from 'react';
import { useState } from 'react';
import { Button, Modal , Card } from 'react-bootstrap';
import RawQuotes from '../quotes.json';
import UploadPostButton from './UploadPostButton.js';
import AddToLibrary from '../AddToLibrary.js';
function uploadingRawQuoteToLocalStorage(){
	let rawQuotesToShow = RawQuotes.slice(0,20);
	let quotesID  = [];
	for(let q of rawQuotesToShow){
		let quote = {
			quoteID:String(Math.floor(Math.random(2)*333333)),
			quoteText:q.quoteText,
			quoteAuthor:q.quoteAuthor,
			uploadedBy:[],
			likedBy:[],
			explainations:[]

		}
		quotesID.push(quote.quoteID);
		// localStorage.setItem(quote.quoteID, JSON.stringify(quote));
	}
	// localStorage.setItem('QuotesID', JSON.stringify(quotesID));
}
export default function ChooseToUploadPostButton(props){
	const [showCTUP, setShowCTUP] = useState(false);
	let rawQuotesID = JSON.parse(localStorage.getItem('QuotesID'));
	let rawQuotesToShow = [];
	for(let r of rawQuotesID){
		rawQuotesToShow.push(JSON.parse(localStorage.getItem(r)));
	}
	return(
		<>
			<Button block variant = 'outline-danger' onClick = {()=>{setShowCTUP(true)}}> Upload Post </Button>

					<Modal show = {showCTUP} onHide = {()=>{setShowCTUP(false)}} >
							<Modal.Header closeButton>
							 		<Modal.Title>Choose To Upload A Post </Modal.Title>
							</Modal.Header>
							<Modal.Body>
									
								{
									rawQuotesToShow.map((item, idx)=>(

									<Card key = {idx} className = 'mb-3'>
										<Card.Body>
													<blockquote className = 'blockquote mb-0'>
														<p> {  item.quoteText }
														</p>
														<footer className = 'blockquote-footer text-right'>
															{item.quoteAuthor}
														</footer>
													</blockquote>
										</Card.Body>
										<Card.Footer className = 'd-flex flex-rows justify-content-between'>
											<UploadPostButton keyID = {props.keyID} closeCTUP = {()=>{setShowCTUP(false)}} rawQuote = {item}/>
											<AddToLibrary keyID = {props.keyID } quoteID = {item.quoteID}/>
										</Card.Footer>
									</Card>
										))
								}
							</Modal.Body>
					</Modal>
		</>
		);
}