import React from 'react';
import { Modal, Button, Badge } from 'react-bootstrap';

export default class LikeButton extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			likes:0,
			showUnlikeModal:false,
			prevent:true
		}
		this.handleLike = this.handleLike.bind(this);
		this.handleDislike = this.handleDislike.bind(this);
	}
	componentDidMount(){
		let post = JSON.parse(localStorage.getItem(this.props.quote));
		let likes = post.likedBy.length;
		this.setState({likes});
	}
	handleLike(){
		let clickerKeyID = localStorage.getItem('CurrentQuotesmanAccountKeyID');
		let clickerProfile = JSON.parse(localStorage.getItem(clickerKeyID));
		
		let clickerLikedPost = clickerProfile.likedPost;
		let quote = JSON.parse(localStorage.getItem(this.props.quote));
		let rawQuote = JSON.parse(localStorage.getItem(quote.quoteID));

		let likedBy = quote.likedBy;

		if(!rawQuote.likedBy.includes(clickerKeyID)){
			rawQuote.likedBy.push(clickerKeyID);
		}

		localStorage.setItem(quote.quoteID, JSON.stringify(rawQuote));
		console.log(rawQuote);
		if(likedBy.includes(clickerKeyID)){
			this.setState({showUnlikeModal:true});
		}

		if(!likedBy.includes(clickerKeyID)){
			likedBy.push(clickerKeyID);
			clickerLikedPost.push(this.props.quote);
			this.setState(state =>({
				likes:state.likes+1
			}))
		}

		clickerProfile.likedPost = clickerLikedPost;
		localStorage.setItem(clickerKeyID, JSON.stringify(clickerProfile));
		quote.likes = likedBy.length;
		quote.likedBy = likedBy;
		localStorage.setItem(this.props.quote, JSON.stringify(quote));

	}
	handleDislike(e){
		e.preventDefault();

		let clickerKeyID = localStorage.getItem('CurrentQuotesmanAccountKeyID');
		let clickerProfile = JSON.parse(localStorage.getItem(clickerKeyID));
		
		let clickerLikedPost = clickerProfile.likedPost;
		let quote = JSON.parse(localStorage.getItem(this.props.quote));

		let likedBy = quote.likedBy;

		if(likedBy.includes(clickerKeyID)){
			likedBy.splice(likedBy.indexOf(clickerKeyID), 1);
			clickerLikedPost.splice(clickerLikedPost.indexOf(this.props.quote), 1);
			this.setState(state=>({
				likes:state.likes-1
			}))
			this.setState({showUnlikeModal:false})
		}

		clickerProfile.likedPost = clickerLikedPost;
		localStorage.setItem(clickerKeyID, JSON.stringify(clickerProfile));
		quote.likes = likedBy.length;
		quote.likedBy = likedBy;
		localStorage.setItem(this.props.quote, JSON.stringify(quote));


	}
	render(){
		return(
			<>
			<Button variant = 'outline-primary' size = 'sm' onClick = {()=>{this.handleLike()}}>
				<Badge variant = 'danger'>{this.state.likes===0?'':this.state.likes}</Badge>
				 Like 
			</Button>
			<Modal show = {this.state.showUnlikeModal} onHide = {()=>{
				this.setState({showUnlikeModal:false})

			}}>
				<Modal.Header closeButton></Modal.Header>
				<Modal.Body>
					<h3> Want to dislike it </h3>
				</Modal.Body>
				<Modal.Footer>
					<Button onClick = {this.handleDislike} variant = 'danger'> Yes  </Button>
					<Button onClick = {()=>{
						this.setState({showUnlikeModal:false})
				}} variant = 'success'> No  </Button>
				</Modal.Footer>
			</Modal>
			</>
			);
	}
}