import React from 'react';
import { ListGroup, Button } from 'react-bootstrap';
import ProfileBar from './ProfileBar.js';
import AddFriendButton from '../QButtons/AddFriendButtton.js';
export default class GlobalAccounts extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			myProfile:JSON.parse(localStorage.getItem(this.props.keyID)),
			suggestedAccounts:[],
			aise:''
		}
		console.log('global account rendered');	
		this.changeSt = this.changeSt.bind(this);
		this.removeThisFromSuggestedAccounts = this.removeThisFromSuggestedAccounts.bind(this);
	}
	componentDidMount(){
		console.log('rendered global friends suggestedAccounts');
		let keyIDs = JSON.parse(localStorage.getItem('QAccountsKeyIDs'));
		let friends = this.state.myProfile.friends;
		let requests = this.state.myProfile.requests;
		let requestSentTo = this.state.myProfile.requestSentTo;
		for(let i = keyIDs.length-1; i>=0; i--){
			if(friends.includes(keyIDs[i])||requests.includes(keyIDs[i])||requestSentTo.includes(keyIDs[i])||keyIDs[i]===this.props.keyID){
				keyIDs.splice(i, 1);
			}
		}
		let suggestedAccounts = [];
		for(let k of keyIDs){
			suggestedAccounts.push(JSON.parse(localStorage.getItem(k)));
		}
		this.props.sendOutSide(suggestedAccounts);
		this.setState({suggestedAccounts:suggestedAccounts});
	}
	removeThisFromSuggestedAccounts(keyID){
		let suggestedAccounts = this.state.suggestedAccounts;
		for(let sa of suggestedAccounts){
			if(sa.keyID===keyID){
				suggestedAccounts.splice(suggestedAccounts.indexOf(sa), 1);
			}
		}
		this.props.updateRequestSentListBy(keyID);
		this.setState({suggestedAccounts:suggestedAccounts});
	}
	changeSt(){
		this.setState({aise:'dkdk'});
	}
	render(){
		return(
			<ListGroup>
				{ 	
					this.props.suggestedAccounts.map((account)=>(
						<ListGroup.Item key = {account.keyID} className = 'd-flex flex-rows'>
							<ProfileBar dpwidth = {'50'} keyID = {account.keyID}/>
							<div>
							<Button variant = 'primary' > Follow  {this.state.aise } </Button>
							<AddFriendButton tokeyID = {account.keyID} fromkeyID = {this.props.keyID} removeThisFromSuggestedAccounts = {this.removeThisFromSuggestedAccounts}/>
								</div>
						</ListGroup.Item>
						))
				}
			</ListGroup>
			);
	}
}

