import React from 'react';
import ahmad from './ahmad.jpg';
import {  Card , Image, ListGroup } from 'react-bootstrap';

import CreateNewAccountButton from './QButtons/CreateNewAccountButton.js';
import FriendButton from './QButtons/FriendButton';
import LoginButton from './QButtons/LoginButton.js';
import MessageListButton from './QButtons/MessageListButton.js';
import ChooseToUploadPostButton from './QButtons/ChooseToUploadPostButton.js';
import MyLibraryButton from './MyLibraryButton.js';
import ProfileBar from './Friends/ProfileBar.js';

export default class ProfileSidebar extends React.Component {
	constructor(props){
		super(props);
		let keyID = (localStorage.getItem('CurrentQuotesmanAccountKeyID'));
		// let myProfile = JSON.parse(localStorage.getItem(keyID));
		// myProfile.myPosts = [];
		// localStorage.setItem(keyID, JSON.stringify(myProfile));
		this.state = {
			gotProfile:true,
			keyID:keyID
		}
	}
	
	render(){
		return (
				<div>
					{
						this.state.gotProfile?
						<div>
							<Card>
								<Card.Header>
									<ProfileBar dpwidth = {'50'} keyID = {this.state.keyID}/>
									</Card.Header>
								<Card.Body>
									<ListGroup variant = 'flush'>
										<ListGroup.Item> <MyLibraryButton keyID = {this.state.keyID } /></ListGroup.Item>
										<ListGroup.Item> <FriendButton keyID = {this.state.keyID }/> </ListGroup.Item>
										<ListGroup.Item> <MessageListButton keyID = {this.state.keyID }/></ListGroup.Item>
										<ListGroup.Item> 
										<ChooseToUploadPostButton keyID = {this.state.keyID}/>
										 </ListGroup.Item>
										
										<hr/>
										<ListGroup.Item> Sign Out </ListGroup.Item>
										<ListGroup.Item> <LoginButton
											updateCurrentProfileBy = {(keyID)=>{
												this.setState({keyID:keyID});
												this.setState({gotProfile:true});
											}}
										 toShow = {'Login '} />  </ListGroup.Item>

										<ListGroup.Item> <CreateNewAccountButton
										updateCurrentProfileBy = {(keyID)=>{
												this.setState({keyID:keyID})
												this.setState({gotProfile:true})
											}}
										/> </ListGroup.Item>
									</ListGroup>
								</Card.Body>
							</Card>
						</div>:
						 <div>
							<Card>
								<Card.Header>
									<Image src = {ahmad}  width = {80} roundedCircle />
									<h3> WELCOME TO QUOTESMAN </h3>
								</Card.Header>
								<Card.Body>
									<ListGroup variant = 'flush'>
										<ListGroup.Item> <LoginButton
											updateCurrentProfileBy = {(keyID)=>{
												this.setState({keyID:keyID})
												this.setState({gotProfile:true})
											}}
										 toShow = {'Login '} />  </ListGroup.Item>


										<ListGroup.Item> <CreateNewAccountButton 
											updateCurrentProfileBy = {(keyID)=>{
												this.setState({keyID:keyID})
												this.setState({gotProfile:true})
											}}
										  />  </ListGroup.Item>

										<ListGroup.Item> <LoginButton
											updateCurrentProfileBy = {(keyID)=>{
												this.setState({keyID:keyID})
												this.setState({gotProfile:true})
											}}
										 toShow = {'Already Have An Account '} />  </ListGroup.Item>
									</ListGroup>
								</Card.Body>
							</Card>
						</div>
					}	
				</div>
			);
	}
}