import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-grid.css';
import 'bootstrap/dist/css/bootstrap-reboot.css';
import { InputGroup, Button, Alert, Form, Col,Modal, Card } from 'react-bootstrap';
export default class CreateNewAccount extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			showPwd:false,
			email:'',
			password:'',
			showInvalidEmailAlert:false,
			invalidEmailAlertText:'',
			showInvalidPasswordAlert:false,
			invalidPasswordAlertText:'',
			showNotMatchedModal:false

		}
		this.handleSubmit = this.handleSubmit.bind(this);
	}
	handleSubmit(e){
		e.preventDefault();
		let canLogin = true;
		/// checking name validation
		  	// emailValidatioon
		  	const email = document.getElementById('email');
		  	const password = document.getElementById('password');
		  if(!this.state.email.includes('@')){
		  		this.setState({showInvalidEmailAlert:true});
		  		this.setState({invalidEmailAlertText:'your email dont contain @ '});
		 	canLogin = false;
		  }
		  if(!this.state.email.includes('yaaho')){
				  if(!this.state.email.includes('gmail')){
						  if(!this.state.email.includes('reddit')){
		  		this.setState({showInvalidEmailAlert:true});
		  		this.setState({invalidEmailAlertText:'your email dont contain any domain name ex. gmail , yaaho, reddit '});
		 	canLogin = false;
						  	
				}
			 }
		  }
		  if(!this.state.email.includes('.com')){
		  		this.setState({showInvalidEmailAlert:true});
		  		this.setState({invalidEmailAlertText:'your email dont contain .com '});
		 	canLogin = false;
		  }
		  if(this.state.email.length===0){
		  		this.setState({showInvalidEmailAlert:true});
		  		this.setState({invalidEmailAlertText:'please enter your email address '});
		 	canLogin = false;
		  }
		 
		  if(this.state.password.length===0){
		  	this.setState({showInvalidPasswordAlert:true});
		  	this.setState({invalidPasswordAlertText:'please enter password'})
		 	canLogin = false;
		  }
			if(this.state.password.length<8){
		  	this.setState({showInvalidPasswordAlert:true});
			this.setState({invalidPasswordAlertText:'please enter 8 word password'});		  	
		 	canLogin = false;
		  	
		}
						// this.props.uploadMyAccount();

						console.log('came');
		if(canLogin){
			let accountkeyIDs = JSON.parse(localStorage.getItem('QAccountsKeyIDs'));
				let matched = false;
				console.log(accountkeyIDs);
				for(let keyID of accountkeyIDs){
					let account = getAccountByKeyID(keyID);

					if(account.password===this.state.password&&account.email === this.state.email){
						localStorage.setItem('CurrentQuotesmanAccountKeyID', account.keyID);						
						matched = true;
						this.props.showProfile();
						this.props.updateCurrentProfileBy(account.keyID);
						this.props.showProfileByKeyID(account.keyID);
				}


				if(!matched){
					this.setState({showNotMatchedModal:true});
					email.value = '';
					password.value = '';
				}
			}
		}

	}
	render(){
		return(
			<div>
				<Card  className = 'mt-5 mb-5'>
					<Card.Body>
						<Form >
						    <Form.Row>
								<Form.Group as = {Col} >
									<Form.Label as = 'h5' >Email </Form.Label>
									<Form.Control  id = 'email' type = 'text' placeholder = 'example@gmail.com'
										onChange = {(e)=>{
												this.setState({email:e.target.value});
												this.setState({showInvalidEmailAlert:false})
										}}
									/>
								</Form.Group>
							</Form.Row>
							<Form.Row>
							<Alert show = {this.state.showInvalidEmailAlert} variant = 'danger' > {this.state.invalidEmailAlertText} </Alert>
							</Form.Row>
							<Form.Row>
								<Form.Group as = {Col} >
									<Form.Label as = 'h5' >Password </Form.Label>
									<InputGroup>
										<Form.Control  id = 'password' type = {this.state.showPwd?'text':'password'}
											onChange = {(e)=>{
												this.setState({password:e.target.value});
												this.setState({showInvalidPasswordAlert:false});
											}}
										 />
										<InputGroup.Append>
												<Button size = 'sm' variant = 'primary' 
													onClick = {()=>{
														this.setState(state=>({showPwd:!state.showPwd}))
													}}
												>
												{ this.state.showPwd?'Hide':'Show' }
												 </Button>
											
										</InputGroup.Append>
									</InputGroup>
								</Form.Group>
							</Form.Row>
							<Form.Row>
							<Alert show = {this.state.showInvalidPasswordAlert} variant = 'danger' > {this.state.invalidPasswordAlertText} </Alert>
							</Form.Row>
							<br/>
							<Button className = 'mt-2' block variant = 'success'
								onClick = {this.handleSubmit}
							>Login </Button>
						</Form>
					</Card.Body>
				</Card>
				<Modal show = {this.state.showNotMatchedModal} onHide = {()=>{
					this.setState({showNotMatchedModal:false})
				}}
				backdrop = 'static'
				>
					<Modal.Header closeButton> Not Matched </Modal.Header>
					<Modal.Body>
						<Card.Body>
							No matched account is available to your <br/>
							email 
						</Card.Body>

						<Card.Footer>
							<Button variant = 'primary' block
							onClick = {()=>{
								this.setState({showNotMatchedModal:false});

							 }}
							 > Try Again </Button>
							<Button variant = 'primary' block 
								onClick = {()=>{
									this.props.closeLogin();
									this.props.showCNA();
									this.setState({showNotMatchedModal:false});
								}}
							 > Create New Account </Button>
						</Card.Footer>
					</Modal.Body>
				</Modal>
			</div>

			);
	}
}


function getAccountByKeyID(keyID){
	let account = JSON.parse(localStorage.getItem(keyID));
	return account;
}