import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-grid.css';
import 'bootstrap/dist/css/bootstrap-reboot.css';
import {   Card,  Button, ListGroup, InputGroup, FormControl  } from 'react-bootstrap';

export default class Sortbar extends React.Component {
		constructor(props){
		super(props);
		this.state = {
			categoriesLengthToShow:4,
			showMoreCategory:true,
			categoriesFilteredToShow:['Spiritual', 'Physicists', "Coders"],
			categoryFilterKey:'',
			categories:['Spiritual', 'Hard i Work', 'Codersi', 'Singersi', 'Comediani', 'Actorsi', 'Biologists', 'Physicists', 'Mathematicians', 'Futurei', 'Politicians','Presidents'],
			trendingCategories:['Coders','Spiritual', 'Physicists'],
			categoriesFiltered:['Spiritual', 'Hard-Work', "Coders"],
			personalities:['APJ Abdul Kalam', 'Mahatma Gandhi', 'Elon Musk', 'Steve Jobs' , 'Bill Gates', 'SwamiVivekananda', 'Mark Zuckerberg'],
			personalitiesLengthToShow:4,
			showMorePersonality:true,
			personalitiesFilteredToShow:['Spiritual', 'Physicists', "Coders"],
			personalityFilterKey:'',
			trendingPersonalities:['Coders','Spiritual', 'Physicists'],
			personalitiesFiltered:['Spiritual', 'Hard-Work', "Coders"]
			
		}
		this.showMoreCategories = this.showMoreCategories.bind(this);
		this.handleCategoryFilter  = this.handleCategoryFilter.bind(this);
		this.showMorePersonalities = this.showMorePersonalities.bind(this);
		this.handlePersonalityFilter  = this.handlePersonalityFilter.bind(this);

	}

	handleCategoryFilter(e){
		let categoriesFiltered = [];
		for(let category of this.state.categories){
			if(category.toLowerCase().includes(e.target.value.toLowerCase())){
				categoriesFiltered.push(category);
			}
			}
			this.setState({categoriesFiltered:categoriesFiltered});
		if(categoriesFiltered.length===0){
			let trendingCategories = this.state.trendingCategories;
			this.setState({categoriesFilteredToShow:trendingCategories});			
		}

		if(categoriesFiltered.length>this.state.categoriesLengthToShow){
			let categoriesFilteredToShow = categoriesFiltered.slice(0,this.state.categoriesLengthToShow);
			this.setState({categoriesFilteredToShow:categoriesFilteredToShow});
		}
		if(categoriesFiltered.length>0&&categoriesFiltered.length<=this.state.categoriesLengthToShow){
		this.setState({categoriesFilteredToShow:categoriesFiltered});
		}

		console.log(categoriesFiltered);
	}

	handlePersonalityFilter(e){
		let personalitiesFiltered = [];
		for(let personality of this.state.personalities){
			if(personality.toLowerCase().includes(e.target.value.toLowerCase())){
				personalitiesFiltered.push(personality);
			}
			}
			this.setState({personalitiesFiltered:personalitiesFiltered});
		if(personalitiesFiltered.length===0){
			let trendingPersonalities = this.state.trendingPersonalities;
			this.setState({personalitiesFilteredToShow:trendingPersonalities});			
		}

		if(personalitiesFiltered.length>this.state.personalitiesLengthToShow){
			let personalitiesFilteredToShow = personalitiesFiltered.slice(0,this.state.personalitiesLengthToShow);
			this.setState({personalitiesFilteredToShow:personalitiesFilteredToShow});
		}
		if(personalitiesFiltered.length>0&&personalitiesFiltered.length<=this.state.personalitiesLengthToShow){
		this.setState({personalitiesFilteredToShow:personalitiesFiltered});
		}

		console.log(personalitiesFiltered);
	}

	showMoreCategories(e){
		e.preventDefault();
		let categoriesFiltered = this.state.categoriesFiltered;
		let showMoreCategory = !this.state.showMoreCategory;
		this.setState({showMoreCategory:showMoreCategory});

		if(this.state.categoriesFiltered.length===0){
			let trendingCategories = this.state.trendingCategories;
			this.setState({categoriesFilteredToShow:trendingCategories});			
		}
		if(!this.state.showMoreCategory){
			let categoriesFilteredToShow = this.state.categoriesFiltered.slice(0,this.state.categoriesLengthToShow);
			this.setState({categoriesFilteredToShow:categoriesFilteredToShow});
	
		}
		if(this.state.showMoreCategory){
			this.setState({categoriesFilteredToShow:categoriesFiltered});
		}
	}
	showMorePersonalities(e){
		e.preventDefault();
		let personalitiesFiltered = this.state.personalitiesFiltered;
		let showMorePersonality = !this.state.showMorePersonality;
		this.setState({showMorePersonality:showMorePersonality});

		if(this.state.personalitiesFiltered.length===0){
			let trendingPersonalities = this.state.trendingPersonalities;
			this.setState({personalitiesFilteredToShow:trendingPersonalities});			
		}
		if(!this.state.showMorePersonality){
			let personalitiesFilteredToShow = this.state.personalitiesFiltered.slice(0,this.state.categoriesLengthToShow);
			this.setState({personalitiesFilteredToShow:personalitiesFilteredToShow});
	
		}
		if(this.state.showMorePersonality){
			this.setState({personalitiesFilteredToShow:personalitiesFiltered});
		}
	}

	render(){
		return(
				<div>
						<h3 className="text-center bg-primary m-0 p-0 pt-2 text-white pb-2"> Sort Quote By </h3>
						<ListGroup >
							<ListGroup.Item  className = "p-0 m-0 "><Button size="sm" variant = "outline-success" block onClick = {this.props.handleMostLiked}> Most Liked </Button></ListGroup.Item>
							<ListGroup.Item  className = "p-0 m-0 "><Button size="sm" variant = "outline-success" block onClick = {this.props.handleLatest}> Latest </Button></ListGroup.Item>
							<ListGroup.Item  className = "p-0 m-0 "><Button size="sm" variant = "outline-success" block onClick = {this.props.handleTrending}> Trending </Button></ListGroup.Item>
							
							<h4 className = 'text-center'> Search By Date </h4>
							<ListGroup.Item>
							<InputGroup>
								<FormControl 
								 type = 'text'
								 placeholder = '12 august 2011'
								 />
								 <InputGroup.Append>
								 	<Button variant = 'primary' >Go</Button>
								 </InputGroup.Append>
							</InputGroup>
							</ListGroup.Item>
							<ListGroup.Item>
								<Card>
									<Card.Header>
										<InputGroup>

											<FormControl 
											type = 'text'
											placeholder = 'search for categories'
											onChange = {this.handleCategoryFilter}
											/>
										</InputGroup>
										<Card.Body>
										<ListGroup>
											{
												this.state.categoriesFilteredToShow.map((item, index)=>(
													<ListGroup.Item key = {index} id = {index} onClick = {()=>{
													}}> {item } </ListGroup.Item>
												))
											}
										<ListGroup.Item>
											<Button variant = 'success' onClick = {this.showMoreCategories} block >{!this.state.showMoreCategory?'Show Less':'Show More'}</Button>
										</ListGroup.Item>
										</ListGroup>
										</Card.Body>
									</Card.Header>
								</Card>
							</ListGroup.Item>
							<ListGroup.Item>
								<Card>
									<Card.Header>
										<InputGroup>

											<FormControl 
											type = 'text'
											placeholder = 'search for personalities'
											onChange = {this.handlePersonalityFilter}
											/>
										</InputGroup>
										<Card.Body>
										<ListGroup>
											{
												this.state.personalitiesFilteredToShow.map((item, index)=>(
													<ListGroup.Item key = {index} id = {index} onClick = {()=>{
													}}> {item } </ListGroup.Item>
												))
											}
										<ListGroup.Item>
											<Button variant = 'success' onClick = {this.showMorePersonality} block > {!this.state.showMorePersonality?'Show Less':'Show More'} </Button>
										</ListGroup.Item>
										</ListGroup>
										</Card.Body>
									</Card.Header>
								</Card>
							</ListGroup.Item>

						</ListGroup>
					

				</div>
			);
	}
}