import React from 'react';
import {ListGroup } from 'react-bootstrap';
import ProfileBar from './ProfileBar.js';
import ChatroomButton from '../QButtons/ChatroomButton.js';
export default class MyFriends extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			friendList:[]
		}
	}
	componentDidMount(){

		let myAccount = JSON.parse(localStorage.getItem(this.props.keyID));
		let friendList = [];
		for(let keyID of myAccount.friends){
			friendList.push(JSON.parse(localStorage.getItem(keyID)));
		}
		this.props.sendOutSide(friendList);
		this.setState({friendList});
	}
	removeFromFriendList(keyID){
		let friendList = this.state.friendList;
		for(let f of friendList){
			if(f.keyID===keyID){
				friendList.splice(friendList.indexOf(f), 1);
			}
		}
		this.setState({friendList});
	}
	render(){
		return(
			<ListGroup>
			 this is MyFriends
				{
					this.props.friendList.map((friend)=>(
					<ListGroup.Item key = {friend.keyID}> 
						<ProfileBar dpwidth = {'40'} keyID = {friend.keyID}/>
						<ChatroomButton updateChatFriendsToShow = {()=>{ console.log('') }} me = {this.props.keyID} friend = {friend.keyID} />					
					</ListGroup.Item>
						))
				}
			</ListGroup>
			);
	}
}

function getAccountByKeyID(keyID){
	return JSON.parse(localStorage.getItem(keyID));
}