import React from 'react';
import { Button, Modal } from 'react-bootstrap';
import ProfileBar from '../Friends/ProfileBar.js';
export default class DenyRequestButton extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			showDenyRequestModal:false
		}
		this.handleDenyRequest = this.handleDenyRequest.bind(this);
	}
	handleDenyRequest(e){
		e.preventDefault();
		let myAccount = JSON.parse(localStorage.getItem(this.props.me));
		let requestSentTo  = myAccount.requestSentTo;
		if(requestSentTo.includes(this.props.friend)){
			requestSentTo.splice(requestSentTo.indexOf(this.props.friend));
		}
		myAccount.requestSentTo = requestSentTo;
		localStorage.setItem(this.props.me, JSON.stringify(myAccount));
	
		let friendAccount = JSON.parse(localStorage.getItem(this.props.friend));
		let requests  = friendAccount.requests;
		if(requests.includes(this.props.me)){
			requests.splice(requests.indexOf(this.props.me));
		}
		friendAccount.requests = requests;
		localStorage.setItem(this.props.friend, JSON.stringify(friendAccount));
		this.setState({showDenyRequestModal:false});

		this.props.removeThisFromRequestSent();
	}
	render(){
		return( <>
			<Button variant = 'info' onClick = {()=>{this.setState({showDenyRequestModal:true})}}> Request Sent </Button>
			<Modal show = {this.state.showDenyRequestModal} onHide = {()=>{this.setState({showDenyRequestModal:false})}}>
				<Modal.Header closeButton>
					<ProfileBar dpwidth = {'50'} keyID = {this.props.friend}/>
				</Modal.Header>
				<Modal.Body>
					<h5> you want to deny request to this account </h5>
					<Button variant = 'danger' onClick = {this.handleDenyRequest} > Deny Request </Button>
					<Button variant = 'primary' onClick = {()=>{this.setState({showDenyRequestModal:false})}}>Cancel </Button>
				</Modal.Body>


			</Modal>
			</>
			);
	}
}