import React from 'react';
import ahmad from './ahmad.jpg';
import Quote from './Quotes.js';
import quotes from './quotes.json';
import 'bootstrap/dist/css/bootstrap.css';
import { Card, Row, Col, Button, Dropdown } from 'react-bootstrap';
import ProfileBar from './Friends/ProfileBar.js';

export default class Profile extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			myProfile:JSON.parse(localStorage.getItem(this.props.keyID))
		}
	}
	render(){
		return(
				<div className = "bg-info d-flex justify-content-center">
					<Card style = {{width:'55rem'}} >
						
						<Card.Header className = 'd-flex  justify-content-center'>
							<ProfileBar dpwidth = {'80'} keyID = {this.props.keyID} />
						</Card.Header>
						<Card.Body>
							<Row className = 'd-flex justify-content-between'>
								<Col className = 'd-flex justify-content-center'>
								<Button variant = 'primary'>My Library </Button>
								</Col>
								<Col className = 'd-flex justify-content-center'>
								<Button variant = 'primary'>Friends </Button>
								</Col>
								<Col className = 'd-flex justify-content-center'>
								<Button variant = 'primary'>Personalities </Button>
								</Col>
							</Row>
							<hr/>
							<Row className = 'd-flex justify-content-between'>
								<Col className = 'd-flex justify-content-center'>

								</Col>
								<Col className = 'd-flex justify-content-center'>
								<Button variant = 'primary'>Messages </Button>
								</Col>
								<Col className = 'd-flex justify-content-center'>
								<Button variant = 'primary'>About </Button>
								</Col>
								<Col className = 'd-flex justify-content-center'>
										<Dropdown>
											<Dropdown.Toggle > Settings </Dropdown.Toggle>
											<Dropdown.Menu>
												<Dropdown.Item eventkey = '1'>Edit Profile </Dropdown.Item>
												<Dropdown.Item eventkey = '2'>Forgot Password </Dropdown.Item>
												<Dropdown.Item eventkey = '3'>Sign Out </Dropdown.Item>
												<Dropdown.Item eventkey = '4'>Sign In </Dropdown.Item>
											</Dropdown.Menu>
										</Dropdown>
								</Col>
							</Row>		
							<hr/>
								<Button variant = 'primary'>My Posts </Button>
								<Button variant = 'primary' className = 'ml-5'>Liked Posts </Button>
									{
										this.state.myProfile.myPosts.map((item, idx)=>(
									<Quote key = {idx} quote= {item}/>

									  ))
									}
						</Card.Body>
					</Card>
				</div>
			);
	}
}
