import React  from 'react';
import { useState } from 'react';
import { Button, Modal } from 'react-bootstrap';
import Friends from '../Friends/Friends2.js';
function FriendButton(props){
	const [showModal, setShowModal] = useState(false);

	return(
		<>
				<Button variant = 'outline-success' block onClick = {()=>{setShowModal(true)}}>Friends </Button>

				<Modal
					 show = {showModal}
					 onHide = {()=>{
						setShowModal(false)
					}}
					size = 'lg'
					centered
					backdrop  = 'static'
					>
					<Modal.Header closeButton>
					</Modal.Header>
					 <Friends keyID = {props.keyID}/>
					</Modal>
				
		</>
		);
}

export default FriendButton;