import React, { useState } from 'react';
import { Button, Modal, Card } from 'react-bootstrap';
import CreateNewAccount from '../CreateNewAccount.js';
function CreateNewAccountButton(props){
	const [showCNA, setShowCNA] = useState(false);

	return(	<>
			<Button block variant = 'outline-success' onClick = {()=>{setShowCNA(true)}}>Create New Account </Button>
				<Modal
					 show = {showCNA}
					 onHide = {()=>{ setShowCNA(false) }}
					size = 'lg'
					centered
					backdrop  = 'static'
					>
					<Modal.Header closeButton>
					<Card.Header  className = 'text-center mb-2 ml-10' as = 'h3'>Create New Account </Card.Header>
					</Modal.Header>
					<CreateNewAccount closeCNA = {()=>{ setShowCNA(false) }}
					updateCurrentProfileBy = {(keyID)=>{props.updateCurrentProfileBy(keyID)}}
					 />
					</Modal>				
			</>
		);
}

export default CreateNewAccountButton;