import React from 'react';
import { Image, Modal } from 'react-bootstrap';
import Profile from '../Profile.js';
export default class ProfileBar extends React.Component{
	constructor(props, children){
		super(props, children);
		this.state = {
			myProfile:JSON.parse(localStorage.getItem(this.props.keyID)),
			showProfileModal:false,
		}
	}
	render(){
		let myProfile = JSON.parse(localStorage.getItem(this.props.keyID));
		let dp = myProfile.profilePicture;
		let dpwidth = this.props.dpwidth;
		let myName = myProfile.firstName + '  ' + myProfile.lastName;
		return(
			<div className = 'd-flex flex-row'>
				<Image src = {dp} width = {dpwidth} roundedCircle 
  					onClick = {()=>{this.setState({showProfileModal:true})}}
				 />
				<h5>  { myName } </h5>

				 <Modal show = {this.state.showProfileModal} onHide = {()=>{this.setState({showProfileModal:false})}}>
				 	<Modal.Header closeButton>
				 	</Modal.Header>
				 	<Profile keyID = {this.props.keyID}/>
				 </Modal>
			</div>
			);
	}
}