import React from 'react';
import { Button, Image,  Modal, Row, Col, Card, FormControl } from 'react-bootstrap';
import Quote from '../Quotes.js';
import Profile from '../Profile.js';
export default class UploadPostButton extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			showUP:false,
			quoteCategory:'',
			explanation:'',
			showProfile:false
		}
		this.handleUploadPost  = this.handleUploadPost.bind(this);
	}
	componentDidMount(){
	}
	handleUploadPost(e){
		e.preventDefault();
			
			let myProfile = JSON.parse(localStorage.getItem(this.props.keyID));
			let quoteToUpload = {
					uploadedBy:myProfile.firstName + '   ' +  myProfile.lastName,
					uploaderID:this.props.keyID,
					uploaderPhoto:myProfile.profilePicture,
					postID:this.props.keyID + String(Date.now()),
					likedBy:[],
					likes:0,
					comments:[],
					categories:[],
					taggedFriends:[],
					date:Date.now(),
					quoteID:this.props.rawQuote.quoteID,
					statement:[this.props.rawQuote.quoteText],
					quoter:this.props.rawQuote.quoteAuthor,
					Explain:[]
				}
		let rawQuote = JSON.parse(localStorage.getItem(this.props.rawQuote.quoteID));

		if(this.state.explanation.length!=0){
			quoteToUpload.Explain.push(this.state.explanation);
			rawQuote.explainations.push(this.state.explanation);
		}
		rawQuote.categories = []
		if(this.state.quoteCategory.length!=0){
			quoteToUpload.categories.push(this.state.quoteCategory);
			rawQuote.categories.push(this.state.quoteCategory);
		}

		rawQuote.uploadedBy.push(this.props.keyID);
		localStorage.setItem(this.props.rawQuote.quoteID, JSON.stringify(rawQuote));

		console.log(rawQuote);
		let myPosts = myProfile.myPosts;
		myPosts.push(quoteToUpload.postID);
		myProfile.myPosts = myPosts;
		localStorage.setItem(this.props.keyID, JSON.stringify(myProfile));

		let qPosts = JSON.parse(localStorage.getItem('qPosts')) || [];
		if(!qPosts.includes(quoteToUpload.postID)){
			qPosts.push(quoteToUpload.postID);
		}
		localStorage.setItem(quoteToUpload.postID, JSON.stringify(quoteToUpload));

		
		this.setState({showUP:false});
		this.setState({showProfile:true});	
	}
	render(){
	return(
		<>
			<Button variant = 'outline-danger'
			block
			 onClick  = {()=>{
				this.setState({showUP:true})
			}}>Upload </Button>

			<Modal show = {this.state.showUP} onHide = {()=>{
							this.setState({showUP:false})
						}}>
					<Modal.Header closeButton >
						<Modal.Title>Upload A Post</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<ShowStatement quote = {this.props.rawQuote} keyID = {this.props.keyID}/>
						<Row className = 'd-flex justify-content-between'>
						<Col className = 'd-flex justify-content-center'> <Button  variant = 'success' size = 'md' ></Button>
						</Col>
						<Col className = 'd-flex justify-content-center'> <Button  variant = 'danger' size = 'md' ></Button>
						</Col>
						<Col className = 'd-flex justify-content-center'> <Button  variant = 'dark' size = 'md' ></Button>
						</Col>
						<Col className = 'd-flex justify-content-center'> <Button  variant = 'warning' size = 'md' ></Button>
						</Col>
						</Row>
						<br/>
						<Card>
							<Card.Header> Add a simple explanation <span className = 'text-muted' as = 'small'>if you want</span> </Card.Header>
							<Card.Body>
								<FormControl
									type = 'textArea'
									onChange = {(e)=>{this.setState({explanation:e.target.value})}}
									/>
							</Card.Body>
						</Card>
						<Button variant = 'primary' className = 'mb-3 mt-3' >Tag Friends </Button>
						<Card>
							<Card.Header> Want to give it a category <span className = 'text-muted' as = 'small'>if you want</span> </Card.Header>
							<Card.Body>
								<FormControl
									type = 'text-area'
									onChange = {(e)=>{
										this.setState({quoteCategory:e.target.value})
									}}
									/>
							</Card.Body>
						</Card>
						
						<br/>
						<Button block  variant = 'success' onClick = {this.handleUploadPost}>Upload </Button>
					</Modal.Body>
					</Modal>


				 <Modal show = {this.state.showProfile} onHide = {()=>{
				 	this.setState({showProfile:false})
					this.props.closeCTUP();

				 }}>
				 	<Modal.Header closeButton>
				 	</Modal.Header>
				 	<Profile keyID = {this.props.keyID}/>
				 </Modal>

			
		</>
		);
	}
}

function ShowStatement(props){
	let myProfile = JSON.parse(localStorage.getItem(props.keyID));
	let profilePicture = myProfile.profilePicture;
	let myName = myProfile.firstName + '  '+ myProfile.lastName;
	let statement = props.quote.quoteText;
	let quoter = props.quote.quoteAuthor;
	return(
			<Card>
				<Card.Header>
					<Image src = { profilePicture } width = {'50'} roundedCircle />
					<h3> { myName } </h3>
				</Card.Header>

				<Card.Body>
					<blockquote className = 'blockquote mb-0'>
						<p> {  statement }
						</p>
						<footer className = 'blockquote-footer text-right'>
							{quoter}
						</footer>
					</blockquote>
				</Card.Body>
			</Card>  
		);
}