import React from 'react';
import { Button, Modal } from 'react-bootstrap';
import Chatroom from '../Messages/ChatRoom.js';
export default class ChatroomButton extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			showChatRoomModal:false
		}
	}
	render(){
		return(
			<>
			<Button variant = 'success' onClick = {()=>{this.setState({showChatRoomModal:true})}} >
				Message
			</Button>
			<Modal show = { this.state.showChatRoomModal } onHide = {()=>{this.setState({showChatRoomModal:false})}}>
				<Modal.Header closeButton ></Modal.Header>
				<Chatroom updateChatFriendsToShow = {()=>{
					this.props.updateChatFriendsToShow();

				}}  sender = {this.props.me} receiver = { this.props.friend }/>
			</Modal>
			</>
			);
	}
}