import React from 'react';
import { Card, InputGroup, FormControl, Button } from 'react-bootstrap';
import ProfileBar from '../Friends/ProfileBar.js';

export default class ChatRoom extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			messageToSend:'',
			messageToDisplay:JSON.parse(localStorage.getItem(this.props.sender)).messages[this.props.receiver]||[]
		}
		console.log(this.state.messageToDisplay);
		this.handleSendMessage = this.handleSendMessage.bind(this);
	}
	handleSendMessage(e){
		e.preventDefault();
		if(this.state.messageToSend.length!==0){
		let sender = JSON.parse(localStorage.getItem(this.props.sender));
		let receiver = JSON.parse(localStorage.getItem(this.props.receiver));

		let message = {
			sent:this.state.messageToSend,
			received:'',
			date:Date.now()
		};

		let senderMessages = sender.messages;
		let chatWithReceiver = senderMessages[this.props.receiver];
		if(chatWithReceiver===undefined){
			chatWithReceiver = [];
		}

		chatWithReceiver.push(message);
		sender.messages[this.props.receiver] = chatWithReceiver;
		let messageToDisplay = this.state.messageToDisplay;
		messageToDisplay.push(message);
		this.setState({messageToDisplay});

		message = {
			sent:'',
			received:this.state.messageToSend,
			date:Date.now()
		};

		let receiverMessages = receiver.messages;
		let chatWithSender = receiverMessages[this.props.sender];
		if(chatWithSender===undefined){
			chatWithSender = [];
		}	
		chatWithSender.push(message);
		receiver.messages[this.props.sender] = chatWithSender;

		localStorage.setItem(this.props.sender, JSON.stringify(sender));
		localStorage.setItem(this.props.receiver, JSON.stringify(receiver));
		this.props.updateChatFriendsToShow();
		this.setState({messageToSend:''});

		}
	}
	render(){
		return(
			<>
				<Card>
					<Card.Header>
					<ProfileBar dpwidth = {'50'} keyID = {this.props.receiver}/>
					</Card.Header>
					<Card.Body>
						{	this.state.messageToDisplay.map((message)=>(
							<div key = {message.date}>
 							   <span className = 'row justify-content-end'  >{message.sent}</span>
 							   <span className = 'row justify-content-start' >{message.received}</span>
 							</div>
							))

						}
					</Card.Body>
					<Card.Footer>
						<InputGroup>
							<FormControl 
							 type = 'text'
							 placeholder = 'type your message'
							 value = { this.state.messageToSend }
							 onChange = {(e)=>{this.setState({messageToSend:e.target.value})}}
							 />
							 <InputGroup.Append>
							 	<Button variant = 'light' onClick = {this.handleSendMessage}> Send </Button>
							 </InputGroup.Append>
						</InputGroup>
					</Card.Footer>
				</Card>
			</>
			);
	}
}


