import React from 'react';
import { Button, Modal, Card, ListGroup } from 'react-bootstrap';
export default class MyLibraryButton extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			showMyLibrary:false,
			quotes:[]
		}
	}
	componentDidMount(){
	 	let myProfile = JSON.parse(localStorage.getItem(this.props.keyID));
	 	let quotesID = myProfile.myLibrary;
	 	let quotes  = [];
	 	for(let q of quotesID ){
	 		quotes.push(JSON.parse(localStorage.getItem(q)));
	 	}
	 	this.setState({quotes});
	}
	render(){
		let quotes = [];
	 	let myProfile = JSON.parse(localStorage.getItem(this.props.keyID));
	 	let quotesID = myProfile.myLibrary;
	 	for(let q of quotesID ){
	 		quotes.push(JSON.parse(localStorage.getItem(q)));
	 		}
	 		return(
			<>
				<Button block variant = 'outline-secondary' onClick = {()=>{this.setState({showMyLibrary:true})}}> My Library </Button>
				<Modal show = {this.state.showMyLibrary} onHide = {()=>{ this.setState({showMyLibrary:false}) }} >
					<Modal.Header closeButton ><h3>My Library </h3> </Modal.Header>
					<Modal.Body>
						<ListGroup>
							{
								quotes.map((quote)=>(
									<ListGroup.Item key = {quote.quoteID}>
										<Card>
											<Card.Header>
												{ quote.quoteAuthor }
											</Card.Header>
											<Card.Body>
												{ quote.quoteText }
											</Card.Body>
											<Card.Footer className = 'd-flex flex-rows justify-content-between'>
												<Button variant = 'light' size ='sm' > <small> uploadedBy </small> { quote.uploadedBy.length===0?0:quote.uploadedBy.length } </Button>
												<Button variant = 'light' > <small> likedBy </small>{ quote.likedBy.length===0?0:quote.likedBy.length} </Button>
												<Button variant = 'light' > <small> explainations </small>{ quote.explainations.length===0?0:quote.explainations.length } </Button>
											</Card.Footer>
										</Card>
									</ListGroup.Item>
									))
							}
						</ListGroup>
					</Modal.Body>
				</Modal>
			</>
			);
	}
}