import React from 'react';
import {Card, Modal,  ListGroup, FormControl,Image, Button} from 'react-bootstrap';
import ChatroomButton from '../QButtons/ChatroomButton.js';
import ProfileBar from '../Friends/ProfileBar.js';

export default class MessageList extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			chatFriends:[],
			unChatFriends:[],
			chatFriendsToShow:[],
			unChatFriendsToShow:[],
			showAllFriends:false
		}
		this.filterFriends = this.filterFriends.bind(this);
		this.updateChatFriendsToShow = this.updateChatFriendsToShow.bind(this);
	}
	componentDidMount(){
		let myProfile = JSON.parse(localStorage.getItem(this.props.keyID));
		let friendKeys = myProfile.friends;
		let myMessages = myProfile.messages;

		let chatFriends = [];
		let unChatFriends = [];
		
		for(let fkey of friendKeys){
			if(myMessages[fkey]===undefined){
				unChatFriends.push(fkey);
			}
			else{
				chatFriends.push(fkey);
			}
		}

		this.setState({unChatFriends});
		this.setState({chatFriends});
		
		this.setState({unChatFriendsToShow:unChatFriends});
		this.setState({chatFriendsToShow:chatFriends});


	}

	filterFriends(e){
		e.preventDefault();
		let unChatFriends = this.state.unChatFriends;
		let chatFriends = this.state.chatFriends;
		let unChatFriendsToShow = [];
		let chatFriendsToShow = [];
		if(e.target.value.length!==0){
			for(let ucf of unChatFriends){
				let account = JSON.parse(localStorage.getItem(ucf));
				if(account.firstName.toLowerCase().includes(e.target.value.toLowerCase())||account.lastName.toLowerCase().includes(e.target.value.toLowerCase())){
					unChatFriendsToShow.push(ucf);
				}
			}
			for(let cf of chatFriends){
				let account = JSON.parse(localStorage.getItem(cf));
				if(account.firstName.toLowerCase().includes(e.target.value.toLowerCase())||account.lastName.toLowerCase().includes(e.target.value.toLowerCase())){
					chatFriendsToShow.push(cf);
				}
			}

			this.setState({unChatFriendsToShow});
			this.setState({chatFriendsToShow});

		}
		else{
			this.setState({unChatFriendsToShow:unChatFriends});
			this.setState({chatFriendsToShow:chatFriends});
		}
	}
	updateChatFriendsToShow(fkeyID){
		let chatFriendsToShow = this.state.chatFriendsToShow;
		let unChatFriendsToShow = this.state.unChatFriendsToShow;

		if(!chatFriendsToShow.includes(fkeyID)){
			chatFriendsToShow.push(fkeyID);
		}
		if(unChatFriendsToShow.includes(fkeyID)){
			unChatFriendsToShow.splice(unChatFriendsToShow.indexOf(fkeyID), 1);
		}

		this.setState({chatFriendsToShow});
		this.setState({unChatFriendsToShow});
	}
	render(){
		return(
			<Card>
				<Card.Header>
					<h3> this chat friends list</h3>
					<FormControl
					type = "text"
					placeholder = 'search for friends'
					onChange = {this.filterFriends}
					/>
				</Card.Header>
				<Card.Body>
					<ListGroup variant = 'flush'>
						{
							this.state.chatFriendsToShow.map((fkeyID)=>(
								<ListGroup.Item key = {fkeyID}>
									<ProfileBar dpwidth = {'50'} keyID = {fkeyID} />
									<ChatroomButton me = { this.props.keyID } friend = {fkeyID} updateChatFriendsToShow = {()=>{this.updateChatFriendsToShow(fkeyID)}} />
								</ListGroup.Item>
							))
						}
						<ListGroup.Item> <Button variant = 'primary' 
						onClick = {()=>{this.setState(state=>({showAllFriends:!state.showAllFriends}))}}>
						 {this.state.showAllFriends?'Show only active friend':"SHOW ALL FRIENDS" } 
						 </Button></ListGroup.Item>
						{ this.state.showAllFriends?<>
						{ 
							this.state.unChatFriendsToShow.map((fkeyID)=>(
							<ListGroup.Item key = {fkeyID} >
								<ProfileBar dpwidth = {'50'} keyID = {fkeyID}/>
								<ChatroomButton updateChatFriendsToShow = {()=>{
									console.log('MessageList')

								}} me = { this.props.keyID } friend = {fkeyID} />
							</ListGroup.Item>
							))
						}
						</>:''
						}
					</ListGroup>
				</Card.Body>
			</Card>
			);
	}
}