import React from 'react';
import { Button } from 'react-bootstrap';

export default class AddFriendButton extends React.Component {
	constructor(props){
		super(props);
	}
	sendFriendRequest(e, fromkey, tokey){
		e.preventDefault();
		let fromAccount = JSON.parse(localStorage.getItem(fromkey));
		let requestSentTo = fromAccount.requestSentTo;
		if(!requestSentTo.includes(tokey)){
			requestSentTo.push(tokey);
		}
		fromAccount.requestSentTo = requestSentTo;
		localStorage.setItem(fromkey,JSON.stringify(fromAccount));

		let toAccount = JSON.parse(localStorage.getItem(tokey));
		let requests = toAccount.requests;
		if(!requests.includes(fromkey)){
			requests.push(fromkey);
		}
		toAccount.requests = requests;
		localStorage.setItem(tokey,JSON.stringify(toAccount));

		this.props.removeThisFromSuggestedAccounts(tokey);
	
	}
	render(){
		return(
				<Button variant = 'primary' onClick = {(e)=>{ this.sendFriendRequest(e, this.props.fromkeyID, this.props.tokeyID)}}> 
					Add Friend </Button>
			);
	}
}