import React from 'react';
import ahmad from './ahmad.jpg';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-grid.css';
import 'bootstrap/dist/css/bootstrap-reboot.css';
import { InputGroup, Button, Alert, Form, Col, Card } from 'react-bootstrap';
export default class CreateNewAccount extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			showPwd:false,
			firstName:'',
			lastName:'',
			email:'',
			password:'',
			showInvalidNameAlert:false,
			invalidNameAlertText:'',
			showInvalidEmailAlert:false,
			invalidEmailAlertText:'',
			showInvalidPasswordAlert:false,
			invalidPasswordAlertText:'',
			showPasswordStrength:false,

		}
		this.handlePasswordStrength = this.handlePasswordStrength.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}
	handlePasswordStrength(e){
		e.preventDefault();
		this.setState({password:e.target.value});
		this.setState({showPasswordStrength:true});
		  let count = 0;
		  let chars = ['!','@','£','"','$','%','^','&','*','(',')','¬','`',',','.','<','>','/','?','-','=','_','+','{','}','[',']',';',':','#','~'];
		  for(let ch of this.state.password.split('')){
		  	if(chars.includes(ch)){
		 	 		count++;
		  	}
		  }

		 if(this.state.password.length>7){
		  if(count/(this.state.password.length+1)>0.75){
		  	this.setState({passwordAlertVariant:'danger'});
			this.setState({invalidPasswordAlertText:'to much character in password'});		  	
		  }
		 if(0.5<count/(this.state.password.length+1)<0.75){
			this.setState({passwordAlertVariant:'success'});
			this.setState({invalidPasswordAlertText:'much strong password'});		  	
		  }
		 if(0.25<count/(this.state.password.length+1)<0.5){
			this.setState({passwordAlertVariant:'info'});
			this.setState({invalidPasswordAlertText:'good password'});
		  }
		  	if(0.25>count/(this.state.password.length+1)){
		  	this.setState({passwordAlertVariant:'danger'});
			this.setState({invalidPasswordAlertText:'weak password add some char ex /?.'});		  	
		  }
		}
		
	}
	handleSubmit(e){
		e.preventDefault();
		let canCreateNewAccount = true;
		/// checking name validation
		 if(Number(this.state.firstName)||Number(this.state.lastName)){
		 	this.setState(state=>({showInvalidNameAlert:true}));
		 	this.setState({invalidNameAlertText:'Number are not allowed in name'});
		 	canCreateNewAccount = false;
		 }
		  if(this.state.firstName.length===0||this.state.lastName.length===0){
			 	this.setState(state=>({showInvalidNameAlert:true}));
				this.setState({invalidNameAlertText:'Please Enter your name '});
		 	canCreateNewAccount = false;
		  }
		  	// emailValidatioon
		  if(!this.state.email.includes('@')){
		  		this.setState({showInvalidEmailAlert:true});
		  		this.setState({invalidEmailAlertText:'your email dont contain @ '});
		 	canCreateNewAccount = false;
		  }
		  if(!this.state.email.includes('yaaho')){
				  if(!this.state.email.includes('gmail')){
						  if(!this.state.email.includes('reddit')){
		  		this.setState({showInvalidEmailAlert:true});
		  		this.setState({invalidEmailAlertText:'your email dont contain any domain name ex. gmail , yaaho, reddit '});
		 	canCreateNewAccount = false;
						  	
				}
			 }
		  }
		  if(!this.state.email.includes('.com')){
		  		this.setState({showInvalidEmailAlert:true});
		  		this.setState({invalidEmailAlertText:'your email dont contain .com '});
		 	canCreateNewAccount = false;
		  }
		  if(this.state.email.length===0){
		  		this.setState({showInvalidEmailAlert:true});
		  		this.setState({invalidEmailAlertText:'please enter your email address '});
		 	canCreateNewAccount = false;
		  }
		 
		  if(this.state.password.length===0){
			this.setState({passwordAlertVariant:'danger'});
		  	this.setState({showInvalidPasswordAlert:true});
		  	this.setState({invalidPasswordAlertText:'please enter password'})
		 	canCreateNewAccount = false;
		  }
			if(this.state.password.length<8){
			this.setState({passwordAlertVariant:'danger'});
		  	this.setState({showInvalidPasswordAlert:true});
			this.setState({invalidPasswordAlertText:'please enter 8 word password'});		  	
		 	canCreateNewAccount = false;
		  	
		}

		// let	key  =  String(Date.now());
		let newHuman = {
					keyID:this.state.firstName + String(Date.now()),
					firstName:this.state.firstName,
					lastName:this.state.lastName,
					email:this.state.email,
					password:this.state.password,
					profilePicture:ahmad,
					myLibrary:[],
					friends:[],
					followers:[],
					following:[],
					requestSentTo:[],
					requests:[],
					favPersonalities:[],
					likedPost:[],
					myPosts:[],
					about:{
							hobbies:'I like to code ',
							profession:'Web Development',
							permanentAddress:'jaddu pipra UP India',
							localAddress:'Delhi',
							currentAddress:'Hyderabad'
					},
					messages:{}
};

		if(canCreateNewAccount){
			localStorage.setItem('CurrentQuotesmanAccountKeyID', newHuman.keyID);
			localStorage.setItem(newHuman.keyID, JSON.stringify(newHuman));
			let accountkeyIDs;
			if(localStorage.getItem('QAccountsKeyIDs')===null){
				accountkeyIDs = [];
			}
			else{
				accountkeyIDs = JSON.parse(localStorage.getItem('QAccountsKeyIDs'));
			}
				accountkeyIDs.push(newHuman.keyID);
				localStorage.setItem('QAccountsKeyIDs', JSON.stringify(accountkeyIDs));
				this.props.closeCNA();
				this.props.updateCurrentProfileBy(newHuman.keyID);
		}


	}
	render(){
		return(
				<div>
						<Card  className = 'mt-5 mb-5'>
							<Card.Body>
						<Form >
								<Form.Row>
									<Form.Label as = 'h5'>Name </Form.Label>
								</Form.Row>
							<Form.Row>
								<Form.Group as = {Col} >
									<Form.Control  type = 'text' placeholder = 'FirstName'
										onChange = {(e)=>{
												this.setState({firstName:e.target.value});
												this.setState({showInvalidNameAlert:false});
													this.setState({showPasswordStrength:false});
										}}
									/>
								</Form.Group>
								<Form.Group as = {Col} >
									<Form.Control type = 'text' placeholder = 'LastName'
										onChange = {(e)=>{
												this.setState({lastName:e.target.value});
												this.setState({showInvalidNameAlert:false});
													this.setState({showPasswordStrength:false});
										}}
									/>
								</Form.Group>
							</Form.Row>
							<Form.Row>
							<Alert show = {this.state.showInvalidNameAlert} variant = 'danger' > {this.state.invalidNameAlertText} </Alert>
							</Form.Row>
						    <Form.Row>
								<Form.Group as = {Col} >
									<Form.Label as = 'h5' >Email </Form.Label>
									<Form.Control  type = 'text' placeholder = 'example@gmail.com'
										onChange = {(e)=>{
												this.setState({email:e.target.value});
												this.setState({showInvalidEmailAlert:false})
													this.setState({showPasswordStrength:false});
										}}
									/>
								</Form.Group>
							</Form.Row>
							<Form.Row>
							<Alert show = {this.state.showInvalidEmailAlert} variant = 'danger' > {this.state.invalidEmailAlertText} </Alert>
							</Form.Row>
							<Form.Row>
								<Form.Group as = {Col} >
									<Form.Label as = 'h5' >Password </Form.Label>
									<InputGroup>
										<Form.Control  type = {this.state.showPwd?'text':'password'}
												onChange = {this.handlePasswordStrength}	
										 />
										<InputGroup.Append>
												<Button size = 'sm' variant = 'primary' 
													onClick = {()=>{
														this.setState(state=>({showPwd:!state.showPwd}))
													}}
												>
												{ this.state.showPwd?'Hide':'Show' }
												 </Button>
											
										</InputGroup.Append>
									</InputGroup>
								</Form.Group>
							</Form.Row>
							<Form.Row>
							<Alert show = {this.state.showInvalidPasswordAlert||this.state.showPasswordStrength} variant = {this.state.passwordAlertVariant} > {this.state.invalidPasswordAlertText} </Alert>
							</Form.Row>
							<Form.File id = 'custom-file'
							label = 'Upload profile picture'
							custom
							/>
							<br/>
							<Button className = 'mt-2' block variant = 'success'
								onClick = {this.handleSubmit}
							>Create Account </Button>
						</Form>
							</Card.Body>

							</Card>
								</div>

			);
	}
}