import React, { useState } from 'react';
import { Modal, Button, Card } from 'react-bootstrap';
import Login from '../Login.js';
import Profile from '../Profile.js';
import CreateNewAccount from '../CreateNewAccount.js';
export default function LoginButton(props){
	const [ showLogin, setShowLogin ] = useState(false);
	const [ showCNA  , setShowCNA   ] = useState(false);
	const [ showProfile, setShowProfile ] = useState(false);
	const [ showProfileByKeyID, setShowProfileByKeyID] = useState(localStorage.getItem('CurrentQuotesmanAccountKeyID'));
	return(
		<>
		<Button variant=  'outline-primary' block onClick = {()=>{setShowLogin(true)}}> { props.toShow } </Button>
			<Modal show = { showLogin } onHide = {()=>{ setShowLogin(false) }}>
					<Modal.Header closeButton>
					</Modal.Header>
					<Login 
						showCNA = {()=>{
							setShowCNA(true);
							setShowLogin(false);
						}}

						showProfile = {()=>{
							setShowProfile(true);
							setShowLogin(false);
						}}
						closeLogin = {()=>{ setShowLogin(false) }}
						updateCurrentProfileBy = {(keyID)=>{props.updateCurrentProfileBy(keyID)}}
						showProfileByKeyID = {( keyID )=>{ setShowProfileByKeyID(keyID) }}
					/>
			</Modal>			
			<Modal
					 show = {showCNA}
					 onHide = {()=>{ setShowCNA(false) }}
					size = 'lg'
					centered
					backdrop  = 'static'
					>
					<Modal.Header closeButton>
					<Card.Header  className = 'text-center mb-2 ml-10' as = 'h3'>Create New Account </Card.Header>
					</Modal.Header>
					<CreateNewAccount closeCNA = {()=>{ setShowCNA(false) }} />
			</Modal>
			<Modal show = {showProfile} onHide = {()=>{ setShowProfile (false )}}>
				 	<Modal.Header closeButton>
				 	</Modal.Header>
				 	<Profile keyID = { showProfileByKeyID }/>
			</Modal>
						
		</>
		);
}