import React from 'react';
import ProfileBar from './Friends/ProfileBar.js';
import { Button, Card, FormControl, ListGroup, InputGroup } from 'react-bootstrap';
export default class CommentButton extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			comment:'',
			postComments:JSON.parse(localStorage.getItem(this.props.postID)).comments
		}
		this.handleAddComment = this.handleAddComment.bind(this);
	}

	handleAddComment(e){
		e.preventDefault();
		let post = JSON.parse(localStorage.getItem(this.props.postID));
		let postComment = post.comments;
		let comment = {
			commentor:this.props.commentor,
			commentorID:this.props.commentorID,
			comment:this.state.comment,
			date:Date.now()
		};
		postComment.push(comment);
		post.comments = postComment;
		localStorage.setItem(this.props.postID, JSON.stringify(post));
		this.setState({postComments:postComment});
	}
	render(){
		return(	
				<>
					<InputGroup>
						<FormControl
						type = "text"
						onChange = {(e)=>{this.setState({comment:e.target.value})}}
						placeholder = 'write your comment here '
						value = {this.state.comment}

						/>
						<InputGroup.Append>
							<Button variant = 'outline-dark' onClick = {this.handleAddComment} > Add Comment </Button>
						</InputGroup.Append>
					</InputGroup>
					<ListGroup>
						{
							this.state.postComments.map((comment)=>(
								<Card key = {comment.date}>
									<Card.Header> <ProfileBar dpwidth = {'40'} keyID = {this.props.commentorID}/></Card.Header>
									<Card.Body>
										{comment.comment}
									</Card.Body>
									<Card.Footer className = 'text-center p-0 text-muted'>
										{comment.date}
									</Card.Footer>
								</Card>
								))
						}

					</ListGroup>
				
				</>
			);
	}
}