import React from 'react';
import { Button , Modal } from 'react-bootstrap';
export default class extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			showRemoveFromLibrary:false,
			textOnButton:''

		}
		this.handleAddToLibrary = this.handleAddToLibrary.bind(this);
		this.handleRemoveFromLibrary = this.handleRemoveFromLibrary.bind(this);
	}
	componentDidMount(){
		let myProfile = JSON.parse(localStorage.getItem(this.props.keyID));
		let myLibrary = myProfile.myLibrary;
		
		if(myLibrary.includes(this.props.quoteID)){
			this.setState({textOnButton:'Remove'})
		}
		else {
			this.setState({textOnButton:'Add'})

		}
	}
	handleAddToLibrary(e){
		e.preventDefault();
		let myProfile = JSON.parse(localStorage.getItem(this.props.keyID));
		let myLibrary = myProfile.myLibrary;
		
		if(myLibrary.includes(this.props.quoteID)){
			this.setState({showRemoveFromLibrary:true});
		}

		if(!myLibrary.includes(this.props.quoteID)){
			myLibrary.push(this.props.quoteID);
			this.setState({textOnButton:'Remove'})
		}
		myProfile.myLibrary = myLibrary;
		localStorage.setItem(this.props.keyID, JSON.stringify(myProfile));


	}
	handleRemoveFromLibrary(e){
		e.preventDefault();

		let myProfile = JSON.parse(localStorage.getItem(this.props.keyID));
		let myLibrary = myProfile.myLibrary;
		
	
		if(myLibrary.includes(this.props.quoteID)){
			myLibrary.splice(myLibrary.indexOf(this.props.quoteID), 1);
			this.setState({textOnButton:'Add'})
		}
		myProfile.myLibrary = myLibrary;
		localStorage.setItem(this.props.keyID, JSON.stringify(myProfile));
		this.setState({showRemoveFromLibrary:false});

	}
	render(){
		return(
			<>
				<Button variant = 'outline-info' onClick = {this.handleAddToLibrary} >{ this.state.textOnButton }</Button>
				<Modal show = {this.state.showRemoveFromLibrary} onHide = {()=>{this.setState({showRemoveFromLibrary:false})}}>
					<Modal.Header closeButton></Modal.Header>
					<Modal.Body> { ' you want to remove this quote from your library '}</Modal.Body>
					<Modal.Footer>
						<Button variant = 'danger' onClick = {this.handleRemoveFromLibrary}> YES </Button>
						<Button variant = 'success' onClick = {()=>{this.setState({showRemoveFromLibrary:false})}}> Cancel </Button>
					</Modal.Footer>

				</Modal>
			</>
			);
	}
}